.nav-item {
	position: relative;
	display: inline-block;
	vertical-align: middle;

	@include media-breakpoint-down(lg) {
		padding: 15px 30px;
		border-bottom: 1px solid rgba($color-white, 0.15);
	}

	&__link {
		display: block;
		border-bottom: 1px solid rgba($color-black, 0.5);
		margin: 9px 45px 9px 0;
		font-size: 18px;
		line-height: 24px;
		text-decoration: none;
		color: inherit;
		transition: color 0.2s, border-bottom-color 0.2s;

		&:hover {
			color: $color-endeavour;
			border-bottom-color: inherit;
			z-index: 2;

			& + .nav-list {
				opacity: 1;
				pointer-events: auto;
			}
		}

		&_sublink {
			cursor: pointer;
			border-bottom: 1px dashed rgba($color-black, 0.5);
		}

		&.active {
			border-color: $color-active-header-link;
			color: $color-endeavour;
		}
	}

	&.active,
	&_active {
		& > .nav-item__link {
			border-color: $color-active-header-link;
			color: $color-endeavour;
		}
	}

	.nav-list {
		position: absolute;
		left: 0;
		top: 100%;
		opacity: 0;
		background-color: $color-endeavour;
		color: $color-white;
		pointer-events: none;
		z-index: 10;
		transition: opacity 0.3s;
		width: 250px;

		@include media-breakpoint-down(lg) {
			position: relative;
			top: 0;
			left: 0;
			width: 100%;
			pointer-events: auto;
			transition: none;
			opacity: 1;
			height: 0;
			overflow: hidden;
		}

		&:hover {
			opacity: 1;
			pointer-events: auto;
		}

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			bottom: 100%;
			height: 45px;
			display: block;
			cursor: pointer;
		}

		.nav-item {
			font-weight: 300;
			padding: 15px 30px;
			border-bottom: 1px solid rgba($color-white, 0.15);
			width: 100%;
			font-size: 18px;

			&:last-of-type {
				margin-bottom: 0;
				border-bottom: none;
			}

			&__link {
				display: inline-block;
				width: auto;
				margin: 0;
			}
		}
	}

	&.show-list {
		& > .nav-list {
			display: block;

			@include media-breakpoint-down(lg) {
				margin-left: -30px;
				margin-right: -30px;
				padding-left: 0;
				height: auto;
				width: auto;

				.nav-item {
					padding-left: 60px;
					padding-right: 60px;
				}
			}
		}
	}
}
